import React, { useState } from 'react';

const SMART_PHONES = 'Smart Phones';
const UTILITY = 'utility';

export default function Products({ setCart, cart }) {
  const [products] = useState([
    {
      category: UTILITY,
      name: 'Toy',
      cost: 50,
      image:
        'data:image/webp;base64,',
    },
    {
        category: UTILITY,
        name: 'Phones',
        cost: 10000,
        image:
          'data:image/jpeg;base64,',
      },
      {
        category: UTILITY,
        name: 'Car',
        cost: 1000000,
        image:
          'data:image/jpeg;base64,',
      },
      {
        category: UTILITY,
        name: 'Toy Car',
        cost: 400,
        image:
          'https://encrypted-tbn2.gstatic.com/shopping?q=tbn:ANd9GcR1PdHcfZ4ll6-DS404AMsV07IBBzsOYWx71AQ6K9IDgUhJgRnpkGXNC4P8veCyvrbZLosxZ_kCVHI0F-nEc071oGMzh4S4JJvdVEvNeCU&usqp=CAE',
      },
      {
        category: UTILITY,
        name: 'AA Battery',
        cost: 50,
        image:
          'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQ5-QAul_NfAs-s0XW9M087xWyPOGWvbfYjmqSl0QXabZRSYoid47i7kISiAteyIh0YOci5mtQ&usqp=CAc',
      },
      {
        category: UTILITY,
        name: 'Toy 2',
        cost: 500,
        image:
          'data:image/webp;base64,',
      },
    {
      category: SMART_PHONES,
      name: 'Smart Phone 1',
      cost: 19000,
      image:
        'https://encrypted-tbn3.gstatic.com/shopping?q=tbn:ANd9GcQYKNqw9rZj3Ut1-6xv75y0chwuWBdtrGW2daqqaw7LR81kkTS7bwbGus5HWhAIgDgym83hAwuEsTDat249zHdIy2gTv3fOhnhP_Xmvc9ip3Ayz0Fp2R9H-&usqp=CAE',
    },
    {
        category: SMART_PHONES,
        name: 'Smart Phone 2',
        cost: 14000,
        image:
          'data:image/webp;base64,',
      },
      {
        category: SMART_PHONES,
        name: 'Smart Phone 3',
        cost: 14000,
        image:
          'data:image/webp;base64,',
      },
      {
        category: SMART_PHONES,
        name: 'Smart Phone 4',
        cost: 14000,
        image:
          'https://encrypted-tbn2.gstatic.com/shopping?q=tbn:ANd9GcTjbbCHmsXF-teg_J7V-TgExx2IDG648nUhqmeRF-99_eYRgY_P8vgGI61_CJTgIpwzp0vZZTjz-P_258SEta4Fl_1-gDARDC_G4LuDYuKsUIyB8Zue85cv0zg&usqp=CAE',
      },
      {
        category: SMART_PHONES,
        name: 'Smart Phone 5',
        cost: 10000,
        image:
          'data:image/webp;base64,',
      },
      {
        category: SMART_PHONES,
        name: 'Blanket',
        cost: 500,
        image:
          'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSpwdYDmUL_ZEqhLV7ZWRdQAU7DGcGaxtCt7SrTlL9umrQs2Un7rj9Nbb9Vq01RtEfA0eAVmdt-&usqp=CAc',
      }
  ]);

  const addToCart = (product) => {
    let newCart = [...cart];
    let itemInCart = newCart.find(
      (item) => product.name === item.name
    );
    if (itemInCart) {
      itemInCart.quantity++;
    } else {
      itemInCart = {
        ...product,
        quantity: 1,
      };
      newCart.push(itemInCart);
    }
    setCart(newCart);
  };

  const [category, setCategory] = useState(SMART_PHONES);

  const getProductsInCategory = () => {
    return products.filter(
      (product) => product.category === category
    );
  };

  return (
    <>
      <div><h1 className="prod">Products</h1></div>
      <b>Select a category:  </b>
      <select className="category" onChange={(e) => setCategory(e.target.value)}>
        <option className="r" value={SMART_PHONES}>{SMART_PHONES}</option>
        <option className="r" value={UTILITY}>{UTILITY}</option>
      </select>
      <div className="products">
        {getProductsInCategory().map((product, idx) => (
          <div className="product" key={idx}>
            <h3>{product.name}</h3>
            <h4>Rs.{product.cost}</h4>
            <img src={product.image} alt={product.name} />
            <br />
            <button className="addtocart" onClick={() => addToCart(product)}>
              <b>Add to Cart</b>
            </button>
          </div>
        ))}
      </div>
    </>
  );
}